<template>
  <div>
    <Title title="杭州市合规网约车平台" />
    <div class="compliance-box">
      <div class="compliance-nav">
        <swiper :options="swiperOption">
          <template v-for="(item,index) in navTitle">
            <swiper-slide class="item" :key="index" :class="{'active':cur==index}">
              <div @click="cur=index">
                <img :src="item.img" />
                <p>{{item.title}}</p>
              </div>
            </swiper-slide>
          </template>
        </swiper>
      </div>
      <div class="compliance-content">
        <ul v-for="(item,index) in navTitle" :key="index" v-show="cur==index">
          <li>
            <label>入职模式：</label>
            <p>公车公营、无车入职{{index}}</p>
          </li>
          <li>
            <label>薪资待遇：</label>
            <p>底薪6520+流水提成，缴纳社保，充电补贴</p>
          </li>
          <li>
            <label>工作时长：</label>
            <p>8小时/天</p>
          </li>
          <li>
            <label>平台特点：</label>
            <p>央企背景、公司配车、收入稳定、奖励高</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import Title from "./Title";

export default {
  data() {
    return {
      swiperOption: {
        slidesPerView: 5,
        freeMode: true,
        preventClicks: false
      },
      navTitle: [
        {
          title: "滴滴出行",
          img: "../../../../static/img/dd.jpg"
        },
        {
          title: "首汽约车",
          img: "../../../../static/img/sq.jpg"
        },
        {
          title: "享道出行",
          img: "../../../../static/img/xd.jpg"
        },
        {
          title: "携华出行",
          img: "../../../../static/img/xh.jpg"
        },
        {
          title: "曹操出行",
          img: "../../../../static/img/cc.jpg"
        },
        {
          title: "T3出行",
          img: "../../../../static/img/T3.jpg"
        }
      ],
      cur: 0
    };
  },
  components: {
    Title
  },
  methods: {
    changeSwiper(index) {
      debugger;
    }
  }
};
</script>
<style lang="stylus" scoped>
.compliance-box {
  margin-top: 0.4rem;
  background: #fff;

  .compliance-nav {
    padding-top: 0.3rem;
    border-bottom: 1px solid #ccc;

    .item {
      text-align: center;
      font-size: 0.2rem;
      color: #afafaf;
      position: relative;
      opacity: 0.5;

      img {
        width: 1rem;
        padding-bottom: 5px;
      }
    }

    .item.active {
      color: #1076ce;
      opacity: 1;
    }

    .active:after {
      position: absolute;
      content: '';
      width: 100%;
      height: 2px;
      background: #1076ce;
      bottom: 0;
      left: 0;
    }
  }

  .compliance-content {
    padding: 0.3rem;

    ul {
      li {
        margin-bottom: 0.2rem;

        label {
          position: relative;
          font-size: 0.3rem;
          color: #1076ce;
          padding-left: 6px;
        }

        label:before {
          position: absolute;
          content: '';
          width: 2px;
          height: 0.3rem;
          background-color: #1076ce;
          left: 0;
          bottom: 0;
          top: 0;
          margin: auto;
        }

        p {
          font-size: 0.4rem;
        }
      }
    }
  }
}
</style>


