<template>
  <div>
    <Title title="司机报名流程" />
    <ul class="flow-wrap">
      <li class="flow-one">
        <span>1</span>
        <label>手机报名</label>
      </li>
      <li class="flow-two">
        <span>2</span>
        <label>信息填写</label>
      </li>
      <li class="flow-three">
        <div class="three-box">
          <span>3</span>
          <label>等待审核</label>
        </div>
      </li>
    </ul>
    <div class="input-box">
      <ul class="input-ul-box">
        <li class="input-item">
          <label>姓名</label>
          <input type="text" placeholder="请输入申请人姓名" v-model="name" maxlength="12" />
        </li>
        <li class="input-item">
          <label>电话</label>
          <input type="tel" placeholder="请输入申请人电话" v-model="phone" maxlength="11" />
        </li>
        <li class="input-item">
          <label>资质</label>
          <div class="redio-box">
            <div
              v-for="redio in redioList"
              :key="redio.value"
              @click="()=>{selectRedio(redio.value)}"
              :class="{'active': selectRedioValue==redio.value}"
            >{{redio.name}}</div>
          </div>
        </li>
      </ul>
      <div class="checkbox-box">
        <h4>期望入职平台</h4>
        <div class="checkbox-list">
          <div
          class="item"
            v-for="checkBox in checkBoxList"
            :key="checkBox.value"
            @click="()=>{selectCheckBox(checkBox.value)}"
            :class="{'active': selectCheckBoxValue.includes(checkBox.value)}"
          >
          <span>{{checkBox.name}}</span>
          </div>
          <div
          v-show="!more"
          class="item"
            @click="clickMore"
          ><span>更多...</span></div>
              <div 
              v-show="more"
              class="item"
              v-for="checkBox in checkBoxListTwo"
              :key="checkBox.value"
              @click="()=>{selectCheckBox(checkBox.value)}"
              :class="{'active': selectCheckBoxValue.includes(checkBox.value)}"
            ><span>{{checkBox.name}}</span></div>
        </div>
        <!-- <ul v-show="more">
          <li
            v-for="checkBox in checkBoxList"
            :key="checkBox.value"
            @click="()=>{selectCheckBox(checkBox.value)}"
            :class="{'active': selectCheckBoxValue.includes(checkBox.value)}"
          >{{checkBox.name}}</li>
        </ul> -->
      </div>
    </div>
    <div class="buttom" @click="submit">立即报名</div>
  </div>
</template>
<script>
import Title from "./Title";
import axios from "axios";
import { Toast } from "mint-ui";

export default {
  data() {
    return {
      redioList: [
        {
          name: "无网约车从业资格证",
          value: "0"
        },
        {
          name: "有网约车从业资格证",
          value: "1"
        }
      ],
      checkBoxList: [
        {
          name: "滴滴出行",
          value: "0"
        },
        {
          name: "首约汽车",
          value: "1"
        },
        {
          name: "享道出行",
          value: "3"
        },
        {
          name: "携华出行",
          value: "4"
        },
        {
          name: "滴滴出行",
          value: "5"
        },
        {
          name: "首约汽车",
          value: "6"
        },
        {
          name: "享道出行",
          value: "7"
        },
        {
          name: "携华出行",
          value: "8"
        },
      ],
      checkBoxListTwo: [
        {
          name: "滴滴出行",
          value: "10"
        },
        {
          name: "首约汽车",
          value: "11"
        },
        {
          name: "享道出行",
          value: "13"
        },
        {
          name: "携华出行",
          value: "14"
        },
        {
          name: "滴滴出行",
          value: "15"
        },
        {
          name: "首约汽车",
          value: "16"
        },
      ],
      name: "",
      phone: "",
      selectRedioValue: "0",
      selectCheckBoxValue: [],
      more: false
    };
  },
  components: {
    Title
  },
  methods: {
    clickMore(){
      this.more = true;
    },
    selectRedio(value) {
      this.selectRedioValue = value;
    },
    selectCheckBox(value) {
      let index = this.selectCheckBoxValue.indexOf(value);
      if (index !== -1) {
        this.selectCheckBoxValue.splice(index, 1);
        return;
      }
      this.selectCheckBoxValue.push(value);
    },
    submit() {
      if (!this.name || !this.phone) {
        Toast("姓名或手机号不允许为空！");
        return;
      }
      let myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (!myreg.test(this.phone)) {
        Toast("请输入正确的手机号！");
        return;
      }
      let params = {
        name: this.name,
        phone: this.phone,
        selectRedioValue: this.selectRedioValue,
        selectCheckBoxValue: this.selectCheckBoxValue.join(",")
      };
      console.log(params);
      axios
        .post("/xxxx", params)
        .then(function(response) {
          Toast("提交成功！");
          this.name = "";
          this.phone = "";
          this.selectRedioValue = "0";
          this.selectCheckBoxValue = [];
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>
<style lang="stylus" scoped>
ul.flow-wrap {
  display: flex;
  margin-top: 0.5rem;
  overflow: hidden;

  li {
    height: 1.2rem;
    display: flex;
    align-items: center;

    span {
      display: block;
      width: 0.5rem;
      height: 0.5rem;
      border-radius: 0.6rem;
      background: #2867d2;
      color: #fff;
      text-align: center;
      line-height: 0.5rem;
      margin-right: 0.2rem;
    }

    label {
      z-index: 2;
    }
  }

  .flow-one {
    flex: 0.9;
    background: #9ebff6;
    position: relative;
    justify-content: flex-end;
  }

  .flow-one:after {
    content: '';
    position: absolute;
    right: -10px;
    background: #9ebff6;
    width: 1rem;
    height: 1rem;
    transform: rotate(-45deg);
    z-index: 1;
    box-shadow: 3px 3px 0px 0px #9ebff6;
  }

  .flow-two {
    flex: 1;
    background: #7fadfa;
    position: relative;
    justify-content: flex-end;
  }

  .flow-two:after {
    content: '';
    position: absolute;
    right: -10px;
    background: #7fadfa;
    width: 1rem;
    height: 1rem;
    transform: rotate(-45deg);
    z-index: 1;
    box-shadow: 3px 3px 0px 0px #7fadfa;
  }

  .flow-three {
    flex: 1.1;
    background: #5997ff;

    .three-box {
      display: flex;
      position: absolute;
      right: 0.6rem;
      align-items: center;
    }
  }
}

.input-box {
  padding: 0.5rem 0.3rem;
  background: #fff;
  margin-top: 0.5rem;

  .input-ul-box {
    border-bottom: 1px solid #ccc;
  }

  .input-item {
    display: flex;
    align-items: center;
    margin-bottom: 0.3rem;

    label {
      flex: 0.1;
      text-align: center;
      font-weight: bold;
      font-size: 0.3rem;
      margin-right: 0.3rem;
    }

    input {
      flex: 0.9;
      height: 1rem;
      background: #f5f6f9;
      border: 1px solid #ccc;
      text-indent: 0.3rem;
      font-size: 0.4rem;
      border-radius: 3px;
    }

    .redio-box {
      flex: 0.9;
      display: flex;
      justify-content: space-between;
      .active {
    position: relative;
    border-color: #1076ce !important;
   
  }

  .active:after {
    position: absolute;
    content: '';
    width: 0.5rem;
    height: 0.5rem;
    background: url('../../../../static/img/selected.png') no-repeat;
    background-size: contain;
    bottom: 0;
    right: -1px;
  }
      div {
        border: 1px solid #ccc;
        padding: 0.3rem;
        font-size: 0.3rem;
        border-radius: 3px;
      }
    }
  }

  .checkbox-box {
    padding-top: 0.4rem;

    .checkbox-list {
      display: flex;
      flex-wrap: wrap;
      margin-top: 0.4rem;
      .item {
        width: 33.33%;
        padding: 0 0.1rem;
        text-align: center;
        font-size: 0.3rem;
        margin-bottom: 0.4rem;
        span{
            display: block;
            margin: 0 auto;
            width: 98%;
            height: 1rem;
            line-height: 1rem;
            border: 1px solid #ccc;
            text-align: center;
            border-radius: 3px;
            overflow: hidden;
        }
      }
      .active {
        span{
          position: relative;
          border-color: #1076ce !important;
        }
        span:after {
          position: absolute;
          content: '';
          width: 0.5rem;
          height: 0.5rem;
          background: url('../../../../static/img/selected.png') no-repeat;
          background-size: contain;
          bottom: 0;
          right: -1px;
        }
      }
    }
  }
}

.buttom {
  height: 1.2rem;
  background-color: #1076ce;
  line-height: 1.2rem;
  text-align: center;
  font-size: 0.4rem;
  font-weight: bold;
  color: #fff;
}
</style>


