<template>
  <div class="notice-wrap">
    <ul class="notice-nav">
      <li
            v-for="(item,index) in navTitle"
            :key="index"
            @click="cur=index"
            :class="{active:cur==index}"
          >
            <img :src="item.img" />
            <p>{{item.title}}</p>
          </li>
    </ul>
    <div>
      <ul class="notice-content" 
      v-for="(item,index) in navTitle" 
      :key="index" v-show="cur==index"
      @click="showModal"
      >
        <li>
          <p>{{index}}关于明确网约车车辆和人员证件办理有关事关于明确网约车车辆和人员证件办理有关事</p>
          <span>9-10</span>
        </li>
        <li>
          <p>关于明确网约车车辆和人员证件办理有关事</p>
          <span>9-10</span>
        </li>

        <li>
          <p>关于明确网约车车辆和人员证件办理有关事</p>
          <span>9-10</span>
        </li>
      </ul>
      <div class="notice-more">
        查看更多
        <img src="../../../../static/img/more.png" />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      navTitle: [
        {
          title: "通知公告",
          img: "../../../../static/img/notice.png"
        },
        {
          title: "行业动态",
          img: "../../../../static/img/hydt.png"
        },
        {
          title: "政策法规",
          img: "../../../../static/img/zcfg.png"
        },
      ],
      cur: 0
    };
  },
  components: {},
  methods:{
    showModal(){
      this.$emit('showModal')
    }
  }
};
</script>
<style lang="stylus" scoped>
.notice-wrap {
  background-color: #fff;
  margin-top: 0.6rem;

  ul.notice-nav {
    display: flex;
    border-bottom: 1px solid #1076ce;

    li {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #1076ce;
      border-right: 1px solid #ccc;
      height: 1.2rem;

      img {
        width: 0.4rem;
        background-size: contain;
        margin-right: 0.2rem;
      }
    }

    li:last-child {
      border-right: none;
    }

    li.active {
      color: #fff;
      background-color: #1076ce;
      border-right: none;
    }
  }

  ul.notice-content {
    padding: 0 0.3rem;

    li {
      padding: 0.3rem 0;
      display: flex;
      border-bottom: 1px dashed #333;

      p {
        flex: 0.9;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      span {
        flex: 0.1;
        color: #afafaf;
        margin-left: 0.3rem;
      }
    }
  }
}

.notice-more {
  color: #1076ce;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0.4rem 0.3rem;

  img {
    width: 0.2rem;
    margin-left: 0.2rem;
  }
}
</style>


