<template>
    <div class="modal-bg" v-show="show">
        <div class="modal-container">
            <div class="modal-header">
               <span @click="hideModal">X</span>
            </div>
            <div class="modal-main">
               <img src="../../../../static/img/code.jpg"/>
               <p>杭州开网约车问题加 客服微信 免费咨询</p>
               <p class="red">↓ 长按下方 复制微信号 ↓</p>
               <p class="yellow">13250849750</p>
               <p class="green">打开微信加好友咨询</p>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'modal',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: ''
        },
    },
    methods: {
        hideModal() {
            this.$emit('hideModal')
        },
    }
}
</script>
<style lang="stylus" scoped>
    .red{
        color:red;
    }
    .yellow{
        background :yellow;
        width:3rem;
        margin :0.4rem auto 0.2rem;
    }
    .green{
        background :green;
         width:4rem;
         margin :auto;
    }
.modal-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.5);
    z-index: 10;
}
.modal-container {
    background: #fff;
    border-radius: 10px;
    overflow: hidden;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.modal-header {
    width:80vw;
    height: 0.8rem;
    border-bottom :1px solid #ccc;
    color: #ccc;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    cursor: move;
    span{
        margin-right :0.4rem;
    }
}
.modal-main {
    padding: 1rem 0.4rem;
    text-align :center;
    img{
        width: 2rem;
        margin-bottom :0.4rem;
    }
}
</style>



