<template>
  <h3>
    <label>{{title || ''}}</label>
  </h3>
</template>
<script>
export default {
  props: ["title"]
};
</script>
<style lang="stylus" scoped>
h3 {
  color: #10529b;
  border-bottom: 1px solid #004795;
  padding: 10px 20px 5px;
  font-weight: bold;

  label {
    position: relative;
  }

  label:after {
    position: absolute;
    content: '';
    width: 100%;
    height: 2px;
    background: #004795;
    bottom: -5px;
    left: 0;
  }
}
</style>


